* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  padding: 2rem;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Inter, sans-serif;
}

.app-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  background: #fff;
  border-radius: 20px;
  margin-bottom: 2rem;
  padding: 2rem;
  box-shadow: 0 10px 40px #0000001a;
}

.header h1 {
  color: #1e40af;
  margin-bottom: .5rem;
  font-size: 2rem;
  font-weight: 700;
}

.header p {
  color: #64748b;
  font-size: 1rem;
}

.overview-container {
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
  display: grid;
}

.overview-content {
  background: #fff;
  border-radius: 20px;
  padding: 2.5rem;
  box-shadow: 0 10px 40px #0000001a;
}

.overview-content h2 {
  color: #1e40af;
  align-items: center;
  gap: .5rem;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  display: flex;
}

.overview-content p {
  color: #475569;
  margin-bottom: 1rem;
  line-height: 1.7;
}

.overview-content ul {
  color: #475569;
  line-height: 1.8;
  list-style: none;
}

.overview-content ul li:before {
  content: "✓ ";
  color: #3b82f6;
  margin-right: .5rem;
  font-weight: bold;
}

.overview-image {
  background: #fff;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  display: flex;
  box-shadow: 0 10px 40px #0000001a;
}

.overview-img {
  object-fit: contain;
  border-radius: 12px;
  width: 100%;
  height: auto;
  max-height: 300px;
}

.start-button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #3b82f6 0%, #1e40af 100%);
  border: none;
  border-radius: 12px;
  align-items: center;
  gap: .5rem;
  margin: 2rem auto 0;
  padding: 1rem 2.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all .3s;
  display: flex;
  box-shadow: 0 4px 20px #3b82f666;
}

.start-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px #3b82f680;
}

.back-button {
  color: #3b82f6;
  cursor: pointer;
  background: #fff;
  border: 2px solid #3b82f6;
  border-radius: 10px;
  align-items: center;
  gap: .5rem;
  margin-bottom: 2rem;
  padding: .75rem 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  transition: all .3s;
  display: flex;
}

.back-button:hover {
  color: #fff;
  background: #3b82f6;
}

.prediction-section, .results-section {
  background: #fff;
  border-radius: 20px;
  margin-bottom: 2rem;
  padding: 2.5rem;
  box-shadow: 0 10px 40px #0000001a;
}

.section-title {
  color: #1e40af;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.modern-form {
  width: 100%;
}

.form-grid {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
  display: grid;
}

.form-group {
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.form-group label {
  color: #475569;
  font-size: .9rem;
  font-weight: 600;
}

.form-group input {
  background: #f8fafc;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  padding: .875rem;
  font-size: 1rem;
  transition: all .3s;
}

.form-group input:focus {
  background: #fff;
  border-color: #3b82f6;
  outline: none;
  box-shadow: 0 0 0 3px #3b82f61a;
}

.predict-button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #3b82f6 0%, #1e40af 100%);
  border: none;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  gap: .75rem;
  width: 100%;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all .3s;
  display: flex;
  box-shadow: 0 4px 20px #3b82f666;
}

.predict-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px #3b82f680;
}

.table-container {
  overflow-x: auto;
}

.modern-table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
}

.modern-table thead tr {
  background: linear-gradient(135deg, #3b82f6 0%, #1e40af 100%);
}

.modern-table th {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .5px;
  padding: 1rem;
  font-size: .9rem;
  font-weight: 600;
}

.modern-table th:first-child {
  border-top-left-radius: 12px;
}

.modern-table th:last-child {
  border-top-right-radius: 12px;
}

.modern-table td {
  text-align: center;
  color: #475569;
  border-bottom: 1px solid #e2e8f0;
  padding: 1rem;
}

.modern-table tbody tr {
  transition: all .2s;
}

.modern-table tbody tr:hover {
  background: #f8fafc;
}

.rank-badge {
  color: #fff;
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  border-radius: 20px;
  padding: .25rem .75rem;
  font-size: .9rem;
  font-weight: 700;
}

.company-code {
  color: #1e40af;
  font-size: 1.1rem;
  font-weight: 700;
}

.status-badge {
  border-radius: 20px;
  padding: .35rem .75rem;
  font-size: .85rem;
  font-weight: 600;
}

.status-badge.positive {
  color: #16a34a;
  background: #dcfce7;
}

.status-badge.negative {
  color: #dc2626;
  background: #fee2e2;
}

.npm-value {
  color: #1e40af;
  font-size: 1.05rem;
  font-weight: 700;
}

.score-bar {
  background: #e2e8f0;
  border-radius: 20px;
  height: 30px;
  position: relative;
  overflow: hidden;
}

.score-fill {
  background: linear-gradient(90deg, #3b82f6 0%, #1e40af 100%);
  height: 100%;
  transition: width .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.score-text {
  z-index: 1;
  color: #fff;
  text-shadow: 0 1px 2px #0000004d;
  font-weight: 700;
  line-height: 30px;
  position: relative;
}

.loading-container {
  text-align: center;
  color: #64748b;
  padding: 3rem;
}

.loading-spinner {
  border: 4px solid #e2e8f0;
  border-top-color: #3b82f6;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 0 auto 1rem;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error-message {
  text-align: center;
  background: #fee2e2;
  border: 2px solid #dc2626;
  border-radius: 12px;
  margin-bottom: 2rem;
  padding: 1.5rem;
}

.error-message p {
  color: #dc2626;
  font-size: 1rem;
  font-weight: 600;
}

.modal-overlay {
  z-index: 1000;
  background: #000000b3;
  justify-content: center;
  align-items: center;
  animation: .3s fadeIn;
  display: flex;
  position: fixed;
  inset: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.modal-content {
  text-align: center;
  background: #fff;
  border-radius: 20px;
  width: 90%;
  max-width: 500px;
  padding: 2.5rem;
  animation: .3s slideUp;
  position: relative;
  box-shadow: 0 20px 60px #0000004d;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-close {
  color: #64748b;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 8px;
  padding: .5rem;
  transition: all .2s;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.modal-close:hover {
  color: #1e293b;
  background: #f1f5f9;
}

.modal-icon {
  color: #ef4444;
  justify-content: center;
  margin-bottom: 1.5rem;
  display: flex;
}

.modal-title {
  color: #1e293b;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.modal-message {
  color: #64748b;
  margin-bottom: 2rem;
  font-size: 1rem;
  line-height: 1.6;
}

.modal-button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #3b82f6 0%, #1e40af 100%);
  border: none;
  border-radius: 10px;
  padding: .875rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  transition: all .3s;
  box-shadow: 0 4px 15px #3b82f666;
}

.modal-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #3b82f680;
}

@media (width <= 768px) {
  body {
    padding: 1rem;
  }

  .overview-container, .form-grid {
    grid-template-columns: 1fr;
  }

  .header h1 {
    font-size: 1.5rem;
  }

  .section-title {
    font-size: 1.25rem;
  }
}
